@mixin buttonOnTodo($right) {
    position: absolute;
    right: $right;
    top: 10px;
    width: 25px;
    height: 25px;
    opacity: 0.7;
    transition: 0.2s;
    cursor: pointer;

    &:hover {
        opacity: 1;
    }

    &.disabled {
        opacity: 0.3;
        cursor: default;
    }
}

.container {
    max-width: 800px;
    margin: 100px auto 0 auto;

    .title {
        text-align: center;
        margin-bottom: 10px;
        color: #aa00ff;
    }

    .todos {
        display: flex;
        align-items: center;
        flex-direction: column;
        padding: 10px 5px;
        margin-top: 10px;

        .todo {
            position: relative;
            list-style: none;
            width: 100%;
            height: auto;
            box-shadow: 2px 2px 10px 0 #8516e0;
            border-radius: 5px;
            padding: 10px 80px 10px 20px;
            font-size: 20px;
            margin-bottom: 10px;

            .text {
                overflow-wrap: break-word;
            }

            .tags {
                margin-top: 8px;
                display: flex;
                flex-wrap: wrap;
            }

            .edit {
                @include buttonOnTodo(40px);
            }

            .delete {
                @include buttonOnTodo(5px);
            }

            &.disabled {
                opacity: 0.3;
                cursor: default;
            }
        }
    }
}
